import {
  getData,
  getDataWithEncrypt,
  postData,
  postDataWithEncrypt,
} from '@/services/api';
import type { IGuideInfo } from '@/types/applyGuide';
import type { IUserInfo } from '@/types/userInfo';
import type {
  VenusCommonConfigType,
  VenusConfigResponseType,
} from '@/types/venusConfig';

/**
 * @param tempCode code
 * @return 
 */
export const doClaimSysLogin = (tempCode: string) => {
  return getDataWithEncrypt<IUserInfo>(
    '/intelli-claim-api/user/tempcode/auth',
    {
      tempCode,
    },
  );
};

/**
 *    
 * @param namespace tthp-config
 * @param key insurances
 * @return 
 */
export const getVenusConfig = (configFileNames: string[]) => {
  return postData<VenusConfigResponseType[]>('/venus-api/config/8', {
    configFileNames,
  });
};
/**
 *    
 * @param namespace tthp-config
 */
export const getVenusCommonConfig = (configFileName: string) => {
  return getData<VenusCommonConfigType>(
    `/venus-api/config/9/${configFileName}`,
  );
};

/**
 * 
 * @param envCode 
 */
export const getGuidInfo = (envCode: string) => {
  return postDataWithEncrypt<IGuideInfo>(
    '/intelli-claim-api/intelli-claim/queryGroupPolicyList-v1',
    { envCode },
  );
};
