/**
 * 
 * config
 * updateConfigcode
 */
import { Sentry } from '@elt_common/common';
import { Toast } from '@elt_common/components';
import { difference } from 'lodash-es';
import { useCallback, useMemo, useState } from 'react';
import { useRecoilState } from 'recoil';

import { getVenusCommonConfig, getVenusConfig } from '@/apis';
import { venusConfigState } from '@/store/venusConfigStore';
import type {
  VenusConfigResponseType,
  VenusConfigValueType,
} from '@/types/venusConfig';

export default () => {
  const [config, setConfig] = useRecoilState(venusConfigState);
  const [commonConfigLoaded, setCommonConfigLoaded] = useState(false);
  const [commonConfigLoadFailed, setCommonConfigLoadFailed] = useState(false);

  const materialUploadKeys = useMemo(() => {
    const materialKeys = Object.entries(config.commonFormOption.material).map(
      ([key, value]) => {
        return { value: key, label: value.label };
      },
    );
    return materialKeys.concat([{ value: 'common', label: '' }]);
  }, [config.commonFormOption.material]);
  /**  { 'C4': '' } */
  const accTypeMap = useMemo(() => {
    const accTypeMapEntries = config.commonFormOption.accTypeOptions.map(
      ({ label, value }) => {
        return [value, label];
      },
    );
    return Object.fromEntries(accTypeMapEntries);
  }, [config.commonFormOption.accTypeOptions]);

  /**  */
  const updateConfig = useCallback(
    async (productCode: string[]) => {
      const originProductCode = Object.keys(config)?.map(
        (item) => config.insuranceType[item]?.productCode,
      );
      const hasDifference = Boolean(
        difference(productCode, originProductCode)?.length,
      );
      if (!Object.keys(config.insuranceType)?.length || hasDifference) {
        try {
          const res = await getVenusConfig(productCode);
          const configData = res?.data?.reduce(
            (
              pre: Record<string, VenusConfigValueType>,
              next: VenusConfigResponseType,
            ) => {
              pre[next.configFileName] = next.value;
              return pre;
            },
            {},
          );
          setConfig({ ...config, insuranceType: configData });
        } catch (error) {
          // 
          Toast.show('');
          console.error('Error fetching config:', error);
          Sentry.reportError(
            'useVenusConfig hook: |: /venus-api/config/8',
            { error },
          );
        }
      }
    },
    [config, setConfig],
  );
  /**  */
  const updateCommonConfig = useCallback(async () => {
    try {
      const res = await getVenusCommonConfig('common');
      setConfig({ ...config, ...res.data });
      setCommonConfigLoadFailed(false);
    } catch (error) {
      // 
      setCommonConfigLoadFailed(true);
      Toast.show('');
      console.error('Error fetching config:', error);
      Sentry.reportError(
        'useVenusConfig hook: |/venus-api/config/9',
        { error },
      );
    } finally {
      setCommonConfigLoaded(true);
    }
  }, [config, setConfig]);

  return {
    config,
    materialUploadKeys,
    accTypeMap,
    updateConfig,
    /**  */
    commonConfigLoaded,
    /**  */
    commonConfigLoadFailed,
    updateCommonConfig,
  };
};
